<template>
  <div style=" _margin-left:20px;" class="new-tabs1">
    <!--   多标签样例-->
    <!--    <el-card :style="{width: windowWidth}" >-->
    <!--      <el-breadcrumb separator-class="el-icon-arrow-right" style="padding:5px;">-->
    <!--        &lt;!&ndash;      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>&ndash;&gt;-->
    <!--        <el-breadcrumb-item-->
    <!--          v-for="(item, index)  in this.$route.query.caption"-->
    <!--          :key="index"-->
    <!--        >{{ customGlobal.decrypt(item,customGlobal.cryptoKey) }}</el-breadcrumb-item>-->
    <!--      </el-breadcrumb>-->
    <!--    </el-card>-->

    <!--    <el-button type="primary" @click="showQuery">确定</el-button>-->


    <el-tabs   v-model="editableTabsValue"  :style="{ height: '200px'}"  @tab-click="trigerChildObj">

      <el-tab-pane
        v-for="(item, index) in editableTabs"
        :key="item.name"
        :label="item.title"
        :name="item.name"


      >
        <component

          v-if=vueObj[index].show
          :is=item.content
          :pri_detail="{
            treeNodeCaptions:$route.query.caption,
            treeNodeIds:$route.query.id,
            treeNodeOrgId:$route.query.orgid,
            currentTreeNode:$route.query.cur_obj,
            treeNodeType:$route.query.type,
            cbInfo:$route.query.cb_info

            }"
          @parent_method="showPublicMsg"
        >

        </component>
      </el-tab-pane>
    </el-tabs>
    <community-builder-set-info ref="cb"></community-builder-set-info>
  </div>

</template>

<script>


import CommunityBuilderSetInfo from "../baseInfoManager/CommunityBuilderSetInfo";

export default {
  name: "OrgInfoCenter",
  components:{CommunityBuilderSetInfo},

  mounted() {

  },
  created(){
    console.info("传递参数："+this.$route.query);
    this.init();

  },
  data() {
    return {
      editableTabsValue: '1',
      editableTabs:[],
      vueObj :[
        {caption:'热力公司',id:'1',vue:'/views/orgInfoManager/OrgInfoCompany.vue',show:true},
        {caption:'热力分公司',id:'2',vue:'/views/orgInfoManager/OrgInfoCompany2.vue',show:false},
        {caption:'热力所',id:'3',vue:'/views/orgInfoManager/OrgInfoInstitute.vue',show:false},
        {caption:'换热站',id:'4',vue:'/views/orgInfoManager/OrgInfoStation.vue',show:false},
        {caption:'小区信息',id:'5',vue:'/views/orgInfoManager/OrgInfoCommunity.vue',show:false},
        {caption:'楼栋信息',id:'6',vue:'/views/orgInfoManager/OrgInfoBuild.vue',show:false},
        {caption:'用户信息',id:'7',vue:'/views/orgInfoManager/OrgInfoUser.vue',show:false}
      ],

    }
  },
  watch: {


  },

  methods:{
    showPublicMsg( args,callback){
      // console.info("返回对象："+JSON.stringify(  this.$refs.cb.showPublicInfo() ));
      // return  this.$refs.cb.showPublicInfo();
      callback(this.$refs.cb.showPublicInfo());
    },
    showQuery(){
      console.info("this.$route.query:"+this.$route.query.orgid);
      // console.info("this.$route.query:"+decodeURIComponent(this.$route.query.caption));
      console.info("this.$route.query:"+this.customGlobal.decrypt(this.$route.query.caption,this.customGlobal.cryptoKey));
      console.info("this.$route.query:"+this.$route.query.id);
    },
    init(){
      for(var i1=0;i1<this.vueObj.length;i1++){
        //console.info("obj:"+this.vueObj[i1].vue);
        let vue = this.vueObj[i1].vue;

        let Acomponent  = () => import('@/components' +  vue).catch((error) => {
          //console.info("出错了："+error);
          this.$message({
            type: 'error',
            showClose: true,
            duration:0,
            dangerouslyUseHTMLString: true,
            // iconClass:'el-icon-info',
            message: '<spand style="color:black;">出错了：' + error +"</spand>"
          });
        });
        this.editableTabs.push({
          title: this.vueObj[i1].caption,
          name: this.vueObj[i1].id,
          content:Acomponent
        });
      }
    },
    trigerChildObj(tab, event){
      // console.log(tab, event);
      // console.info("editableTabsValue:"+this.editableTabsValue);

      console.info("tab.name:"+tab.name);
      for (var i1=0;i1<this.vueObj.length;i1++){
        this.vueObj[i1].show = false;
      }
      this.vueObj[tab.name-1].show = true;


    }
  }
}
</script>

<style scoped>
.new-tabs1 /deep/ .el-tabs__item{
  width:100px!important;
  text-align: center!important;
}

</style>



